.wrapper {
	// max-width: 1200px;
	// margin: 20px auto;
	// padding: 0 15px;

	> h1 {
		text-align: center;

		span {
			display: inline-block;
			padding: 0 10px 10px;
			transition: all 0.5s linear;
		}
	}
}

.tab_wrapper {
	display: inline-block;
	width: 100%;
	position: relative;

	* {
		box-sizing: border-box;
	}

	> ul {
		clear: both;
		display: inline-block;
		width: 100%;
		border-bottom: 1px solid rgba(0, 40, 100, 0.12);
		margin-bottom: 0;

		li {
			float: left;
			cursor: pointer;
			font-weight: 500;
			padding: 8px 18px;
			text-transform: uppercase;
			border: 1px solid #f0f2f7;
			font-size: 1.05em;
			border-bottom: 0;
			position: relative;
			border-top: 3px solid #f0f2f7;
			margin: 0 5px 0 0;

			&.active:after {
				content: "";
				position: absolute;
				bottom: -1px;
				height: 1px;
				left: 0;
				background: #fff;
				width: 100%;
			}
		}
	}

	.controller {
		display: block;
		overflow: hidden;
		clear: both;
		position: absolute;
		top: 0;
		right: 0;

		span {
			padding: 8px 22px;
			text-transform: uppercase;
			display: inline-block;

			&.next {
				float: right;
				margin-left: 10px;
			}
		}
	}

	.content_wrapper {
		float: left;
		width: 100%;
		border: 1px solid rgba(0, 40, 100, 0.12);
		border-top: 0;
		box-sizing: border-box;

		.tab_content {
			display: none;
			padding: 15px 20px;

			.error {
				color: red;
			}
		}

		.accordian_header {
			display: none;
			padding: 10px 20px;
			text-transform: uppercase;
			font-weight: bold;
			border-top: 1px solid #f0f2f7;
			border-bottom: 1px solid #fff;
			position: relative;
			cursor: pointer;

			&.active {
				border: 1px solid #f0f2f7;

				&:after {
					position: absolute;
					top: 0;
					left: 0;
					content: "";
					width: 5px;
					height: 100%;
				}
			}

			.arrow {
				display: inline-block;
				width: 12px;
				height: 12px;
				position: relative;
				border-radius: 2px;
				transform: rotate(-135deg);
				background: #fff;
				border-top: 3px solid #f0f2f7;
				border-left: 3px solid #f0f2f7;
			}

			&.active .arrow {
				transform: rotate(45deg);
				margin-top: 5px;
			}
		}
	}

	&.left_side {
		> ul {
			width: 25%;
			float: left;
			border-bottom: 1px solid #f0f2f7;

			li {
				width: 100%;
				box-sizing: border-box;
				margin: 0;
				padding: 12px 18px;
				border-width: 1px;
				border-right: 0;
				border-left: 0;

				&:after {
					left: 0;
					top: 0;
					content: "";
					position: absolute;
					width: 5px;
					height: 100%;
					background: #f0f2f7;
				}

				&.active {
					border-color: #f0f2f7;

					&:after {
						left: 0;
						top: 0;
						width: 5px;
						height: 100%;
					}

					&:before {
						right: -1px;
						position: absolute;
						top: 0;
						content: "";
						width: 2px;
						height: 100%;
						background: #fff;
					}
				}
			}
		}

		.content_wrapper {
			width: 75%;
			border: 1px solid #f0f2f7;
			float: left;
		}

		.controller {
			top: -50px;
		}
	}

	&.right_side {
		> ul {
			width: 25%;
			float: right;
			border-bottom: 1px solid #f0f2f7;

			li {
				width: 100%;
				box-sizing: border-box;
				margin: 0;
				padding: 11.3px 18px;
				border-width: 1px;
				border-right: 0;
				border-left: 0;
				text-align: right;

				&:after {
					right: 0;
					left: inherit;
					top: 0;
					content: "";
					position: absolute;
					width: 5px;
					height: 100%;
					background: #f0f2f7;
				}

				&.active {
					border-color: #f0f2f7;

					&:after {
						right: 0;
						left: inherit;
						top: 0;
						width: 5px;
						height: 100%;
						content: "";
					}

					&:before {
						left: -2px;
						right: inherit;
						position: absolute;
						top: 0;
						content: "";
						width: 2px;
						height: 100%;
						background: #fff;
					}
				}
			}
		}

		.content_wrapper {
			width: 75%;
			float: left;
			border: 1px solid #f0f2f7;
		}

		.controller {
			top: -50px;
		}
	}

	&.accordion {
		.content_wrapper {
			border-color: #f0f2f7;

			.accordian_header {
				display: block;
			}
		}

		> ul {
			display: none;
		}
	}

	.active_tab {
		display: none;
		position: relative;
		background: #000000;
		padding: 12px;
		text-align: center;
		padding-right: 50px;
		color: #fff;
		cursor: pointer;
		border-radius: 30px;

		.arrow {
			width: 30px;
			height: 30px;
			overflow: hidden;
			line-height: 30px;
			text-align: center;
			background: #fff;
			position: absolute;
			right: 6px;
			border-radius: 50%;
			top: 50%;
			transform: translateY(-50%);

			&:after {
				content: "";
				width: 10px;
				display: inline-block;
				height: 10px;
				border-right: 3px solid #000;
				border-bottom: 3px solid #000;
				transform: rotate(45deg);
				position: absolute;
				top: 50%;
				left: 50%;
				margin-left: -6px;
				margin-top: -8px;
			}
		}

		.text {
			text-indent: 39px;
			display: block;
		}
	}
}

@media only screen and (max-width: 768px) {
	.tab_wrapper {
		> ul {
			display: none;
		}

		.content_wrapper {
			border: 0;

			.accordian_header {
				display: block;
			}
		}

		&.left_side .content_wrapper,
		&.right_side .content_wrapper {
			width: 100%;
			border-top: 0;
		}

		.controller {
			display: none;
		}

		&.show-as-dropdown {
			> .tab_list {
				border: 0;
				box-shadow: 0 0 8px #f0f2f7;
				position: absolute;
				z-index: 3;
				background: #fff;
				border-radius: 5px;
				margin-top: 15px;

				&:before {
					content: "";
					position: absolute;
					top: -6px;
					left: 50%;
					width: 14px;
					height: 14px;
					background: #fff;
					transform: rotate(45deg);
					box-shadow: -3px -4px 5px #efefef;
					margin-left: -7px;
				}

				li {
					border: 0;
					border-bottom: 1px solid #f0f2f7;

					&.active:after {
						display: none;
					}
				}
			}

			.active_tab {
				display: block;
			}

			> {
				.tab_list,
				.content_wrapper > .accordian_header {
					display: none;
				}

				.tab_list li {
					display: block;
					float: none;
					width: 100%;
				}
			}
		}
	}
}

@media only screen and (min-width: 769px) {
	.tab_wrapper.show-as-dropdown > .tab_list {
		display: block !important;
		overflow: hidden;
	}
}

