}

.badge-danger-transparent {
  color: $danger;
  background-color: rgba($danger,0.15);
}

.badge-purple-transparent {
  color: $purple;
  background-color: rgba($purple,0.15);
}

.badge-info-transparent {
  color: #0a7ffb;
  background-color: rgba($info, 0.15);
}

.badge-pink-transparent {
  color: $pink;
  background-color: rgba($pink,0.15);
}

.badge-light-transparent {
  color: $muted;
  background-color: rgba($muted,0.15);
}

.badge {
  display: inline-block;
  padding: 4px 6px 4px 6px;
  font-weight: 400;
  line-height: 1.1;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: 3px;
  font-size: 75%;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

@media (prefers-reduced-motion: reduce) {
  .badge {
    transition: none;
  }
}

a.badge {
  &:hover, &:focus {
    text-decoration: none;
  }
}

.badge:empty {
  display: none;
}

.btn .badge, .sp-container button .badge {
  position: relative;
  top: -1px;
}

.badge-pill {
  padding-right: 0.6em;
  padding-left: 0.6em;
  border-radius: 10rem;
}

