.intl-tel-input {
	position: relative;
	border-color: #e1e6f1;

	.hide {
		display: none;
	}

	.flag-dropdown {
		position: absolute;
		z-index: 1;
		cursor: pointer;
		height: 40px;
		line-height: 40px;

		.selected-flag {
			margin: 1px 2px 1px 1px;
			padding: 11px 16px 11px 6px;
			background: #f4f4fb;
			border-radius: 5px 0px 0px 5px;

			&:hover {
				background-color: #eaeef9;
			}
		}
	}
}

.country-list .highlight {
	float: inherit;
}

.intl-tel-input {
	.flag-dropdown {
		.selected-flag .down-arrow {
			top: 6px;
			position: relative;
			left: 20px;
			width: 0;
			height: 0;
			border-left: 4px solid transparent;
			border-right: 4px solid transparent;
			border-top: 4px solid black;
		}

		.country-list {
			list-style: none;
			padding: 0;
			margin: 0;
			z-index: 1;
			overflow-y: scroll;
			background-color: white;
			border: 0;
			position: absolute;
			top: 42px;
			width: 250px;
			max-height: 200px;
			box-shadow: 0px 4px 7px rgba(154, 154, 204, 0.1);

			.divider {
				padding-bottom: 5px;
				margin-bottom: 5px;
				border-bottom: 1px solid #eaf0ff;
			}

			.country {
				line-height: 16px;
				padding: 10px 10px;
				font-size: 14px;

				.dial-code {
					color: #9097b5;
				}

				&.highlight {
					background-color: #f9f9fe;
					font-size: 14px;
					border: 0;
					border-radius: 0;
					margin: 0;
				}
			}

			.flag1 {
				display: inline-block;
				vertical-align: bottom;
				margin-right: 6px;
			}

			.country-name {
				margin-right: 6px;
			}
		}
	}

	input {
		box-sizing: border-box;
		-moz-box-sizing: border-box;
		height: 40px;
		padding-left: 47px;
		position: relative;
		z-index: 0;
		border: 1px solid #e1e6f1;
		width: 250px;
		border-radius: 5px 0px 0px 5px;

		&:focus {
			outline: none;
		}
	}
}

