}

@media (min-width: 992px) {
  .wizard.vertical>.content {
    width: 70%;
    padding: 25px;
  }
}

@media (min-width: 576px) {
  .wizard.vertical>.actions {
    width: 80%;
    float: right;
    border-left: 1px solid $border;
  }
}

@media (min-width: 768px) {
  .wizard.vertical>.actions {
    width: 85%;
  }
}

@media (min-width: 992px) {
  .wizard.vertical>.actions {
    width: 70%;
    padding: 25px;
  }
}

/****** EQUAL COLUMN WIDTH STEP INDICATOR *****/

.step-equal-width>.steps>ul {
  display: flex;

  >li {
    flex: 1;
    width: auto;
    float: none;
    flex: 1;
    width: auto;
    float: none;
  }

  display: flex;
}

/****** EQUAL COLUMN WIDTH STEP INDICATOR *****/

/***** CUSTOM STYLES *****/

.wizard-style-1>.steps>ul {
  a {
    padding: 0;
    height: 50px;

    &:hover,
    &:active {
      padding: 0;
      height: 50px;
    }

    .number,
    &:hover .number,
    &:active .number {
      width: 50px;
      height: 100%;
      border: 0;
      font-size: 18px;
      font-weight: bold;
      color: $gray-600;
      background-color: $gray-300;
      border-radius: 0;
    }

    .title,
    &:hover .title,
    &:active .title {
      margin-right: 20px;
      margin-left: 20px;
    }
  }

  .current a {

    .number,
    &:hover .number,
    &:active .number {
      background-color: #452efa;
      color: $white;
    }
  }

  .done a {

    .number,
    &:hover .number,
    &:active .number {
      background-color: #643ab0;
      color: $white;
    }
  }
}

.wizard-style-2>.steps>ul {
  a {
    padding: 0;
    height: 50px;
    border-radius: 50px;

    &:hover,
    &:active {
      padding: 0;
      height: 50px;
      border-radius: 50px;
    }

    .number,
    &:hover .number,
    &:active .number {
      width: 50px;
      height: 100%;
      border: 2px solid $gray-200;
      font-size: 18px;
      font-weight: bold;
      color: $gray-600;
      background-color: $white;
    }

    .title,
    &:hover .title,
    &:active .title {
      margin-right: 20px;
    }
  }

  .current a {

    .number,
    &:hover .number,
    &:active .number {
      border-color: $primary;
      color: $primary;
    }
  }

  .done a {

    .number,
    &:hover .number,
    &:active .number {
      border-color: $purple;
      color: $purple;
    }
  }
}

@media (min-width: 992px) and (max-width:1200px){
  .wizard.vertical>.steps .current a .title {
    display: none;
  }
  .wizard > .steps > ul li .title {
    display: none;
  }
  .wizard.vertical > .steps ul li .title {
    display: none;
  }
}